.App1 {
  font-family: "Arial", sans-serif;
  background-color: #f4f4f4;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
}

.header1 {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #000;
  color: white;
  padding: 15px 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.header1 h1 {
  margin: 0;
  font-size: 1.8em;
}

.logout-btn {
  background-color: transparent;
  color: white;
  border: none;
  cursor: pointer;
  font-size: 1.2em;
  transition: color 0.3s ease;
}

.logout-btn:hover {
  color: #ffd700;
}

.nav1 {
  margin: 20px 0;
  width: 100%;
  display: flex;
  justify-content: center;
}

.nav1 ul {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  gap: 20px;
}

.nav1 li a {
  text-decoration: none;
  color: #000;
  font-size: 1.2em;
  transition: color 0.3s ease, transform 0.3s ease;
}

.nav1 li a:hover {
  color: #0056b3;
  transform: translateY(-2px);
}

.container1 {
  width: 100%;
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.profile,
.orders,
.transactions,
.wishlist {
  margin: 0;
}

.profile h2,
.orders h2,
.transactions h2,
.wishlist h2 {
  color: #000;
  margin-bottom: 20px;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
}

table th,
table td {
  border: 1px solid #ddd;
  padding: 12px;
  text-align: left;
}

table th {
  background-color: #000;
  color: white;
}

table tr:nth-child(even) {
  background-color: #f4f4f4;
}

.wishlist-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 20px;
}

.wishlist-item {
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 20px;
  text-align: center;
}

.wishlist-item h3 {
  color: #333;
  margin-bottom: 10px;
}

.wishlist-item img {
  max-width: 100%;
  border-radius: 8px;
}

.button1 {
  display: block;
  width: 100%;
  padding: 10px 0;
  margin-top: 10px;
  background-color: #000;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.button1:hover {
  background-color: #218838;
  transform: translateY(-2px);
}

.remove-btn {
  display: block;
  width: 100%;
  padding: 10px 0;
  margin-top: 10px;
  background-color: #dc3545;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.remove-btn:hover {
  background-color: #c82333;
  transform: translateY(-2px);
}
.account-container {
  display: flex;
  min-height: 100vh;
  background-color: #f4f4f4;
}

.sidebar2 {
  width: 250px;
  background-color: #000;
  color: white;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 20px;
}

.header2 {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 40px;
}

.header2 h1 {
  font-size: 1.4em;
  margin: 0;
}

.logout-btn {
  background-color: transparent;
  color: white;
  border: none;
  cursor: pointer;
  font-size: 1.2em;
  transition: color 0.3s ease;
}

.logout-btn:hover {
  color: #ffd700;
}

.nav2 {
  width: 100%;
}

.nav2 ul {
  list-style: none;
  padding: 0;
  margin: 0;
  width: 100%;
}

.nav2 li {
  margin-bottom: 20px;
}

.nav2 li a {
  text-decoration: none;
  color: white;
  font-size: 1.2em;
  display: block;
  padding: 10px;
  transition: background-color 0.3s ease, transform 0.3s ease;
  border-radius: 4px;
}

.nav2 li a:hover {
  background-color: #000;
  transform: translateX(5px);
}

.content {
  flex-grow: 1;
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin: 20px;
}
.profile {
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
  max-width: 600px;
  margin: 0 auto;
}

.profile h2 {
  color: #000;
  margin-bottom: 20px;
}

.profile p {
  font-size: 1.1em;
  margin: 10px 0;
}

.profile form div {
  margin-bottom: 15px;
}

.profile form label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

.profile form input {
  width: 100%;
  padding: 8px;
  font-size: 1em;
  border-radius: 4px;
  border: 1px solid #ccc;
}

.profile button {
  padding: 10px 20px;
  background-color: #000;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-right: 10px;
}

.profile button:hover {
  background-color: #365203;
}

.profile button[type="button"] {
  background-color: #6c757d;
}

.profile button[type="button"]:hover {
  background-color: #5a6268;
}
